.query-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: end;
        justify-content: flex-end;
    padding: 20px;
    border-bottom: 1px lightgray solid;
}

.search-input {
    display: -ms-flexbox;
    display: flex;
    padding: 0;
    -ms-flex-preferred-size: 200px;
        flex-basis: 200px;
    -webkit-transition: flex .5s ease;
    -o-transition: flex .5s ease;
    transition: flex .5s ease;
    transition: flex .5s ease, -ms-flex .5s ease;
    outline: none;
}

.search-input:focus {
    -ms-flex-preferred-size: 80%;
        flex-basis: 80%;
    border-right: 0px;
    border-color: rgba(0,149,255,0.5);
}

.search-input:hover {
    border-color: rgba(0,149,255,0.5);
}

.search-button {
    margin-left: 0 !important;
    width: 60px;
}



